import React, { useEffect, useState } from 'react';

// UI Elements
import { Icon, latLng, LatLng, LatLngBounds } from 'leaflet';
import { Map, Marker, TileLayer } from 'react-leaflet';
import DriftMarker from '../components/DriftMarker';
import EncounterDialog from "../components/EncounterDialog";

// Services
import MockMapService from '../services/MockMapService';

// Pathfinding imports
import npath from 'ngraph.path';
import GraphBuilder from '../services/GraphBuilder';
import Queue from '../models/Queue';
import { Graph, Node } from "ngraph.graph";

export function RpgMap(props: any) {
    // const pos = { lat: -51.505, lng: -0.09 } as any;
    const initialPos = latLng([0.007497, 0.007118]);
    const initialNode = "common-room";

    let [graph, setGraph] = useState(null as Graph<any, any> | null); // The map graph data
    let [pos, setPos] = useState(initialPos); // The current (target) position of the player marker
    let [currentVertex, setCurrentVertex] = useState(initialNode); // The current vertex that the player marker is at
    let [vertexQueue] = useState(new Queue<any>()); // A queue of points to visit during path-finding
    let [encounterId, setEncounterId] = useState(null as string | null);

    // Calculates the distance between two points
    const distance = (x: LatLng, y: LatLng): number => {
        const latDist = x.lat - y.lat;
        const lngDist = x.lng - y.lng;
        return Math.sqrt(latDist * latDist + lngDist * lngDist);
    };

    // Build verticies from the graph
    // TODO: Is there an easier method to use?
    const getVertices = (graph: Graph<any, any> | null): Node<any>[] => {
        if (!graph) return [];

        var vertices: Node<any>[] = [];
        graph.forEachNode((n: Node<any>) => {
            vertices.push(n);
        });

        return vertices;
    };

    // Get the vertex nearest to the clicked point
    const findNearestVertex = (graph: Graph<any, any> | null, pos: LatLng): any => {
        const vertices = getVertices(graph);

        for (let i = 0; i < vertices.length; i++) {
            const vertex = vertices[i];

            const dist = distance(pos, vertex.data["position"]);
            const isInRadius = dist < vertex.data["clickRadius"];

            if (isInRadius) {
                return vertex;
            }
            // console.log(`Distance to ${vertex.id} is (${dist} < ${vertex.clickRadius}) = ${isInRadius}`);
        }

        return null;
    };

    // Fire when marker move has finished
    const handleMoveEnd = (marker: any) => {
        console.log("Move ended");

        var targetVertex = vertexQueue.pop();
        if (targetVertex) {
            console.log(`Next vertex ${targetVertex.id}`);

            setPos(targetVertex.data["position"]);
            setCurrentVertex(targetVertex.id);

            const targetEncounterId = targetVertex.data["encounterId"];
            if (targetEncounterId) {
                // console.log(`Set encounter ${targetEncounterId}`);
                setEncounterId(targetEncounterId);
            }
        }
    };

    // Fire when user clicks on the map
    const handleClick = (e: any) => {
        if (!graph) return; // Map not loaded

        console.log("clicked at: " + e.latlng);

        // Move to the nearest vertex to the clicked point
        const match = findNearestVertex(graph, e.latlng);

        if (match) {
            console.log(`Clicked on vertex ${match.id}`);

            var pathFinder = npath.aStar(graph);
            let path = pathFinder.find(currentVertex, match.id);

            if (path.length <= 1) {
                console.log(`No route found to ${match.id}`);
                return;
            }

            path.pop(); // Remove the current/start node

            vertexQueue.clear();
            let nextVertex = path.pop();
            while (nextVertex) {
                vertexQueue.push(nextVertex);
                // Stop if a vertex with an an encounter is hit
                if (nextVertex.data["encounterId"]) {
                    break;
                }
                nextVertex = path.pop();
            }

            handleMoveEnd(null); // Trigger the move to the next vertex
        }
    };

    const moveToVertex = (vertex: string, graph: Graph<any, any>) => {
        const vertices = getVertices(graph);
        const match = vertices.find((v) => v.id == vertex);
        if (match) {
            vertexQueue.push(match);
            handleMoveEnd(null);
        }
        else {
            console.error(`Can't find vertex ${vertex}`);
        }
    };

    const handleEncounterClose = (moveTo: string | undefined) => {
        console.log("Close encounter");
        setEncounterId(null);

        if (moveTo && graph) {
            moveToVertex(moveTo, graph);
        }
    };

    useEffect(() => {
        // Initial load of map data
        const loadMap = () => {
            const service = new MockMapService();
            const data = service.getMap("temple");
            if (!data) {
                console.error(`No map data`);
                return;
            }
            console.log(`Found ${data?.vertices.length} vertices`);
            const graph = GraphBuilder.Build(data);
            setGraph(graph);

            // Mpve to the initial node
            moveToVertex(initialNode, graph);
        };

        if (graph == null) {
            loadMap();
        }
    });

    
    const bounds = new LatLngBounds(latLng([0.0, 0.0]), latLng([0.009634, 0.009642]));
    const maxBounds = new LatLngBounds(latLng([-0.0002, 0.0]), latLng([0.01025, 0.012]));

    var markerIcon = new Icon({
        iconUrl: "images/marker.png",
        iconSize: [50, 75],
        iconAnchor: [25, 65],
        // popupAnchor: [-3, -76],
        // shadowUrl: 'my-icon-shadow.png',
        // shadowSize: [68, 95],
        // shadowAnchor: [22, 94]
    });

    var bullseyeIcon = new Icon({
        iconUrl: "images/blank.svg",
        iconSize: [50, 50],
        className: "vertex-icon",
    });

    const renderVertexMarkers = (graph: Graph<any, any> | null) => {
        const vertices: any[] = getVertices(graph);

        for (let i = 0; i < vertices.length; i++) {
            const v = vertices[i];
            // console.log(`marker at ${v.data['position']} is ${v.id}`);
        }

        return vertices.map((v) => <Marker key={v.id} position={v.data["position"]} title={v.id} icon={bullseyeIcon} />);
    };

    // maxBounds={bounds}
    return (
        <Map
            zoom={18}
            minZoom={18}
            maxZoom={18}
            bounds={bounds}
            maxBounds={maxBounds}
            scrollWheelZoom={false}
            onClick={handleClick}
            zoomControl={false}
        >
            <EncounterDialog onClose={handleEncounterClose} encounterId={encounterId} />
            <TileLayer url="tiles/{z}/{x}/{y}.png" bounds={bounds} />
            <DriftMarker duration={1000} position={pos} keepAtCenter={false} icon={markerIcon} onMoveEnd={handleMoveEnd} />
            {renderVertexMarkers(graph)}
        </Map>
    );
}

export default RpgMap;
